<template>
  <div class="container">
    <el-row>
      <el-col :span="8">
        <h1 class="title">{{pageName}}</h1>
        <label for="">供应商名称</label>
        <el-input v-model="data.name" placeholder="请输入名称"></el-input>
        <label for="">地区</label>
        <el-input v-model="data.area" placeholder="请输入名称"></el-input>
        <label for="">平台</label>
        <el-input v-model="data.platform" placeholder="请输入名称"></el-input>
        <label for="">采购价</label>
        <el-input v-model="data.purchase_price" placeholder="请输入名称"></el-input>
        <label for="">最少量</label>
        <el-input v-model="data.minimum_quantity" placeholder="请输入名称"></el-input>
        <label for="">联系人</label>
        <el-input v-model="data.link_name" placeholder="请输入名称"></el-input>
        <label for="">联系电话</label>
        <el-input v-model="data.link_phone" placeholder="请输入名称"></el-input>
        <label for="">邮箱</label>
        <el-input v-model="data.email" placeholder="请输入名称"></el-input>

      </el-col>
    </el-row>
    <div class="footerButton border-shadow">
      <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
    </div>
  </div>
</template>

<script>
    export default {
        name: "addTask",
        data() {
            return {
                pageName: '新增',
                task: {
                    supplier_platform: '',
                    supplier_name: '',
                    memo: ''
                },
                data:{
                    name:'',
                    area:'',
                    platform:'',
                    purchase_price:'',
                    minimum_quantity:'',
                    link_name:'',
                    link_phone:'',
                    email:'',
                },
                categoryList: [],
            }
        },
        methods: {
            handleChange(value) {
                let length = value.length;
                this.data.cid = value[length-1];
            },
            submitTask() {
                let url = "/v1/suppliers";
                let params = {};
                params.name = this.data.name;
                params.area = this.data.area;
                params.platform = this.data.platform;
                params.purchase_price = this.data.purchase_price;
                params.minimum_quantity = this.data.minimum_quantity;
                params.link_name = this.data.link_name;
                params.link_phone = this.data.link_phone;
                params.email = this.data.email;

                let that = this;
                this.$axios.post(url,params).then((res) => {
                    console.log(res);

                    that.$message({
                        type: 'success',
                        message: '新增成功!'
                    });

                }).catch((err) => {

                    that.$message({
                        type: 'error',
                        message: '新增失败!'
                    });
                })
            },
        },
        created() {

        }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



